.collect-page {
    flex: 1;
    display: flex;
    flex-direction: row;
    min-height: 100%;
    height: 100%;
    width: 100%;
    /*background-color: #161619;*/

    .pushable:active.pushable:hover {
        transform: scale(1) !important;
        animation-name: click-btn !important;
        animation-duration: 0.2s;
    }

    .pushable:hover {
        transform: scale(1.1);
        animation-name: hover-btn !important;
        animation-duration: 0.2s;
    }



    .connect-yr-wallet {
        position: absolute;
        right: 10px;
        top: 15px;
        background-color: white;
        color: black;
        padding: 5px;
        font-size: 14pt;
        border-radius: 8px;
        cursor: pointer
    }

    .connect-yr-wallet:active {
        transform: scale(1);
        animation-name: click-wallet;
        animation-duration: 0.2s;
    }

    .connect-yr-wallet:hover {
        transform: scale(1.1);
        animation-name: hover-wallet;
        animation-duration: 0.2s;
    }

    .sidebar {
        height: 100%;
        width: 200px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        .search-container {
            position: relative;
            margin-top: 15px;

            .search-icon {
                width: 15px;
                height: 15px;
                position: absolute;
                left: 15px;
                top: 15px;
            }
        }

        input.collection-search {
            color: white;
            margin: 5px;
            border-radius: 15px;
            background-color: #323232;
            padding: 5px;
            padding-left: 30px;
            outline: none !important;
            border: none !important;
        }

        .number-of-works {
            margin-left: 15px;
            margin-top: 15px;
            display: flex;

            .dot {
                background-color: white;
                border-radius: 5px;
                margin-top: 13px;
                width: 5px;
                height: 5px;
                margin-left: 5px;
                margin-right: 5px;
            }
            
        }

        .user-id-button {
            background-color: #686868;
            color: #ffffff;
            padding: 5px;
            border-radius: 5px;
            cursor: pointer;
            text-align: center;
            /* align-items: flex-start; */
            margin-left: 15px;
            margin-top: 15px;
            /* margin-right: auto; */
            display: flex;

            .user-name {
                border-right: 1px solid #959595;
                padding-left: 5px;
                padding-right: 5px;
                margin-right: 5px;
            }

            .ellipse {
                line-height: 20px;
                font-size: 12pt;
                margin-right: 5px;
            }
        }

        .collect-header {
            font-size: 38px;
            margin-top: 20px;
            margin-left: 20px;
            text-orientation: sideways;
            writing-mode: vertical-lr;
        }

        img.logo-hiero {
            margin-top: auto;
            margin-left: 20px;
            width: 160px;
        }
    }

    .works-scroll {
        flex: 1;
        width: 100%;
        height: 100%;
        max-height: 100%;
        overflow-y: scroll;
    }


    .minted-collection {
        .collection-works {
            display: flex;
            flex-wrap: wrap;
        }
        .minted-work:active {
            //transform: scale(1.1);
            //animation-name: grow-little;
            //animation-dur: 0.1s;
        }
        .minted-work {
            cursor: pointer;
            background-color: #0e0e0e;
            //border-radius: 15px;
            margin: 7px;
            width: 300px;
            width: 260px;
            // height: 350px;
            overflow: hidden;
            border-radius: 10px;

            .work-info {
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: column;
                
                .work-thumbnail {
                    // width: 298px;
                    height: 300px;
                    max-height: 300px;
                    min-height: 300px;
                    height: 300px;
                    background-size: cover;
                    object-fit: contain;
                }
                
                .work-thumbnail-info {
                    // flex: 1 1;
                    color: white;
                    display: flex;
                    padding-left: 10px;
                    padding-right: 10px;
                    padding-bottom: 10px;
                    // margin: 10px;
                    //background-color: #141414;
                    color: white;
                    border-radius: 10px;
                    display: flex;
                    flex-direction: row;

                    .work-creator {
                        color: gray;
                        margin-left: auto;
                    }

                }

                .pricing {
                    background-color: #171818;
                    flex: 1;
                    padding:10px;
                    font-size: 9pt;
                    .type-and-bpm {
                        display: flex;
                        .pricing-type, .bpm-label {
                            color: gray;
                        }
                        .bpm {
                            display: flex;
                            margin-left: auto;

                            .bpm-value {
                                margin-right: 5px;
                            }
                        }
                    }
                    .price-amount {
                        display: flex;

                        .auction-timer {
                            margin-left: auto;
                        }
                    }
                }
            }
        }

        .collect-nft-viewer {
            animation-duration: 0.8s;
            animation-name: show-work;
            height: 100%;
            max-height: 100%;
            // background-color: #161619;
            color: white;

            .zora-fullCreatorOwnerSection,  .zora-infoContainer {
                border-color: gray;
            }

            .zora-colectionTagWrapper {
                color: white;
            }

            .zora-addressLink {
                color: gray;
            }

            .zora-fullPlaceOfferButton, .zora-infoContainer, .zora-fullProofLink {
                li {
                    color: gray;
                }
                color: gray;
            }

            .title-and-back {
                display: flex;
                width: 100%;
                padding-left: 20%;
                padding-right: 20%;
                margin-bottom: 2px;
                .work-name {
                    flex: 1;
                    border-left: 2px solid #161619; 
                    font-size: 16pt;
                    padding-left: 15px;
                    line-height: 40px;
                    background-color: black;
                    color: white;
                }
            }
            .zora-mediaLoader {
                left: -5%;
                top: -5%;
            }
            .go-to-project {
                margin-left: auto;
                border-left: 2px solid #161619; 
                font-size: 10pt;
                cursor: pointer;
                padding-left: 15px;
                line-height: 40px;
                background-color: black;
                color: white;
                padding-right: 15px;
            }
            .back-btn-container {
                background-color: black;
                cursor: pointer;
                padding-top: 1px;
                width: 50px;
                height: 40px;

                .back-btn {
                    margin-left: 10px;
                    cursor: pointer;
                    height: 0px;
                    margin-top: 12px;
                    width: 0px;
                    border-bottom: 6px solid transparent;
                    border-top: 6px solid transparent;
                    border-right: 8px solid white;
                    position: relative;
                    
                    &:after {
                        width: 14px;
                        height: 4px;
                        background-color: white;
                        content: "";
                        position: absolute;
                        left: 10px;
                        top: -2px;
                    }
                }
            }
        }

        .collection-works.hide {
            //animation-duration: 0.8s;
            //animation-name: hide-works;

            height: 0px;
            max-height: 0px;
            overflow: hidden;
        }
        .collection-works {
            display: flex;
            flex-wrap: wrap;
        }

        .minted-work {
            color: white;

            .zora-cardAuctionPricing {
                border-top-color: #161619;
            }

            .zora-cardOuter {
                background-color: black;
                border: none;
                width: 270px;

                .zora-cardItemInfo {
                    span {
                        color: transparent;
                    }
                }

                .zora-addressLink {
                    color: gray;
                }

                .zora-cardMediaWrapper {
                    width: 270px;
                    height: 270px;
                }
            }
        }
    }
    //padding-top: 50px;
    //padding-left: 10%;
    //padding-right: 10%;

    .zuser-name {
        display: flex;
        font-size: 32pt;
        text-align: center;
        margin-bottom: 15px;
    }
}

@keyframes hide-works {
    from{
        height: 100%;
        max-height: 100%;
    }
    
    to{
        height: 0px;
        max-height: 0px;
    }
}

@keyframes show-work {
    to {
        height: 100%;
        max-height: 100%;
    }
    
    from{
        height: 0px;
        max-height: 0px;
    }
}

@keyframes grow-little {
    from{
        transform: scale(1);
    }
    
    to{
        transform: scale(1.1);
    }
}




@keyframes hover-wallet {
    from {
        transform: scale(1);
    }

    to {
        transform: scale(1.1);
    }
}

@keyframes click-wallet {
    from {
        transform: scale(1.1);
    }

    to {
        transform: scale(1);
    }
}


@keyframes hover-btn {
    from {
        transform: scale(1);
    }

    to {
        transform: scale(1.1);
    }
}

@keyframes click-btn {
    from {
        transform: scale(1.1);
    }

    to {
        transform: scale(1);
    }
}

