$gray: #9aa2aa;
$white: #ccd7d7;


.collect-page {
    position: relative;

    .launch:hover, .cancel:hover, .place-bid:hover, .claim-button:hover {
        transform: scale(1.1);
        animation-name: hover-wallet;
        animation-duration: 0.2s;
    }
    
    .launch:active, .cancel:active, .place-bid:active, .claim-button:active {
        transform: scale(1);
        animation-name: click-wallet;
        animation-duration: 0.2s;
    }
 

    .auction-ends, .current-bid {
        margin-right: 3px;
        color: $gray;
    }

    .auction-flow.bid-flow {
        .bid-error {
            color: #ffbc83;
        }

        .bid-amount {
            cursor: pointer;
            color: #ffffff;
            background-color: #ffffff21;
            border-radius: 8px;
            padding: 4px;
            margin-right: 4px;
            margin-left: 4px;
        }
    }
    .auction-flow {
        position: absolute;
        background-color: #000000bf;
        backdrop-filter: blur(4px);
        width: 100%;
        height: 100%;
        top: 0px;
        left: 0px;

        .progress-bar {
            position: relative;
            top: unset;
            left: unset;
            transform: unset;
        }

        .flow-inner {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            margin-top: 10%;
            margin-left: auto;
            margin-right: auto;
            background-color: #302f2f59;
            background-image: radial-gradient(#914e4ead, #2dcdd517);
            padding: 20px;
            border-radius: 20px;
            width: 500px;

            .zora-field {
                .field-name {
                    color: #b5b5b5;
                }
            }
            .auction-description {
                margin-bottom: 10px;
            }

            .buttons-bottom {
                display: flex;
                margin-top: 25px;
                width: 100%;
                .cancel {
                    border-radius: 10px;
                    align-items: flex-start;
                    padding: 5px;
                    font-size: 13pt;
                    background-color: red;
                    color: $white;
                    cursor: pointer;
                }

                .launch.disabled {
                    opacity: 0.2;
                    pointer-events: none;
                }

                .launch {
                    border-radius: 10px;
                    align-items: flex-start;
                    padding: 5px;
                    font-size: 13pt;
                    background-color: #3ee196;
                    background-image: linear-gradient(
                        45deg, #3bc5db, transparent);
                    margin-left: auto;
                    color: black;
                    cursor: pointer;
                }

           }
            .auction-header {
                font-size: 22pt;
                text-align: center;
                margin-bottom: 10px;
            }
        }
    }


    .work-information {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding-left: 20%;
        padding-right: 20%;
        color: $white;

        .work-bids {
            display: flex;
            flex-direction: column;

            .bid-item {
                display: flex;

                .sold-to {
                    margin-right: 3px;
                }

                .bidder {
                }

                .bid-amount {
                    margin-left: 3px;
                }

                .bidder, .bid-amount {
                    color: $gray;
                    margin-right: 5px;
                }
            }
        }

        .description {
            margin-bottom: 20px;
        }

        a, .creator-name, .bpm {
            font-size: 13pt;
            color: $white !important;
        }
 

        .work-overview {
            display: flex;
            flex-direction: column;
            
            width: 100%;
            border-bottom: 1px solid $gray;
            padding-bottom: 10px;
            margin-bottom: 10px;
            .creator-fields {
                display: flex;
                width: 100%;
                font-size: 11pt;
                
                .reserve-price {
                    color: $gray;

                    .price-amount, .time-left {
                        color: $white;
                        margin-left: 5px;
                        margin-right: 5px;
                    }

                }
                .setup-auction-button, .bpm, .not-for-sale, .reserve-price, .auction-finished {
                    display: flex;
                    margin-left: auto;
                    align-items: flex-start;

                    .place-bid, .claim-button {
                        cursor: pointer;
                        align-items: flex-start;
                        margin-left: 5px;
                        background-color: $white;
                        border-radius: 5px;
                        color: black;
                        padding-left: 5px;
                        padding-right: 5px;
                        cursor: pointer;
                    }
                }
           }
        }

        .collect-button {
            align-items: flex-start;
            background-color: $white;
            border-radius: 10px;
            color: black;
            padding: 10px;
            font-size: 12pt;
            cursor: pointer;
        }
        
        .setup-auction-button {
        }
    }
}
